html,
body {
  height: 100%;
  background: #ececec;
  font-size: 10px;
}

.main,
.slide {
  height: calc(100% - 70px);
}

header {
  background: #fff;
}

.banner {
  display: flex;
  flex-direction: row;
  align-items: center;
}

nav {
  margin-left: auto;
}

.search {
  margin-left: auto;
}

nav ul {
  display: flex;
}

nav a {
  padding: 2.5rem;
  display: inline-block;
  font-size: 1.6rem;
  text-decoration: none;
  text-transform: uppercase;
}

.slide {
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-template-rows: 50px 100px 1fr auto 1fr 100px 50px;
  grid-gap: 5px;
}

.slide-content {
  grid-column: 2 / 6;
  grid-row: 3 / 6;
  z-index: 2;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.slide-image {
  grid-column: 3 / 9;
  grid-row: 2 / 7;
  z-index: 1;
}

.slide-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.slide-content h2 {
  font-size: 13rem;
  line-height: 1;
}

.slide-content .category {
  font-size: 1.4rem;
  text-transform: uppercase;
  font-weight: bold;
  display: flex;
  align-items: center;
}

.slide-content .category:before {
  content: "";
  display: inline-block;
  height: 3px;
  width: 5rem;
  background: #000;
  margin-right: 1rem;
}

.slide-content p {
  font-size: 2rem;
  font-weight: bold;
}

.slide-content a.btn {
  display: block;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 1.4rem;
  font-weight: bold;
  background: #000;
  color: #fff;
  padding: 1.5rem 4rem;
  width: 10rem;
  text-align: center;
  position: relative;
  margin-left: 1rem;
  border-radius: 2px;
}

.slide-content a.btn:after {
  content: "";
  display: block;
  border: 1px solid #000;
  padding: 1rem;
  position: absolute;
  width: 100%;
  height: 100%;
  top: -10px;
  left: -10px;
  border-radius: 2px;
}
